.dws-progress-bar {
 position: absolute;
 top: 50%;
 left: 50%;
 transform: translate(-50%, -50%);
 z-index: 100;
}
.progress-percentage {
 position: absolute;
 top: 50%;
 left: 50%;
 transform: translate(-50%, -50%);
 font-size: 40px;
 font-family: "Verdana", sans-serif;
 font-weight: bold;
 color: #fff;
}
#preloader {
 position: fixed;
 left: 0;
 top: 0;
 right: 0;
 bottom: 0;
 z-index: 100;
}

html * {
 text-rendering: optimizeLegibility;
 -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale;
}
.main-preloader {
 position: fixed;
 width: 100%;
 height: 100%;
 left: 0;
 top: 0;
 z-index: 999999999;
 background-color: #2B2E38;
 text-align: center;
}
.main-preloader .main-preloader-inner {
 position: absolute;
 top: 50%;
 width: 100%;
}
.main-preloader .preloader-percentage {
 color: #ffffff;
 margin: 0;
}
.main-preloader .preloader-percentage span {
 display: inline-block;
 float: center;
 font-size: 50px;
}
.blog-header {
 background-size: cover;
 text-align: center;
 margin-bottom: 50px;
}
.blog-overlay {
 background: #2B2E38;
 color: #fff;
}